/*-- Animations for superlike start --*/
@keyframes superlike-hand {
  0% {
    transform: translate3d(-$animated-icon-width, 0, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes superlike-stars {
  0% {
    fill: $superlike-gray;
    transform: scale3d(1, 1, 1);
  }
  50% {
    fill: $icon-pink;
    transform: scale3d(1.3, 1.3, 1.3);
  }
  100% {
    transform: scale3d(1, 1, 1);
    fill: $icon-pink;
  }
}

@keyframes superlike-message {
  0% {
    transform: scale3d(0, 0, 0);
  }
  80% {
    transform: scale3d(1.05, 1.05, 1.05);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

/*-- Animations for superlike end --*/

/*- Superlike classes start -*/
.superlike-hand {
  @include animation(superlike-hand, $animation-duration: 0.5s);
}
.superlike-stars {
  @include animation(superlike-stars, $animation-delay: 0.2s);
  transform-origin: center;
  transform-box: fill-box;
}

.superlike-message {
  @include animation(superlike-message);
  transform-origin: center;
}

/*- Superlike classes end -*/
